.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}
h1 {
  margin-bottom: 1rem;
  margin-top: 2rem;
}
.alert.info {
  background-color: transparent;
  padding: 1rem 3rem;
  color: rgb(131, 130, 130);
  border-radius: .5rem;
  font-size: 1rem;
  width: max-content;
  text-align: center;
}

input {
  width:  300px;
  align-items: center;
  border-radius: 1rem;
  border: 3px solid rgb(79, 131, 162);
  cursor: pointer;
  transition: 0.3s;
  margin-top: 1rem;
  padding: 1rem;
}

textarea {
  width: 90%;
  border: 10px solid white;
  border-radius: 1rem;
  padding: 1rem;
  margin-top: 2rem;
  opacity: 0;
}
textarea.active {
  opacity: 1;
}